.spinner {
    width: 80px;
    height: 80px;

    border: 2px solid var(--main-background);
    border-top: 3px solid var(--icons);
    border-radius: 100%;
    margin: auto;
    animation: spin 1s infinite linear;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
